<template>
	<div class="pt-20 pb-20">
		<Row :gutter="20">
			<Col :xs="23" :sm="12" :md="12" :lg="12" class="mb-20">
				<Card class="pd-20 ml-20">
					<iframe :src="articleUrl" frameborder="0" height="1000"></iframe>
				</Card>
			</Col>

			<Col :xs="23" :sm="12" :md="12" :lg="11" :class="addPaddingCls?'ml-20':null">
				<Card class="card">
					<iframe
						:src="videoUrl"
						scrolling="no"
						width="100%"
						border="0"
						height="350"
						frameborder="no"
						framespacing="0"
						allowfullscreen="true"
					></iframe>
				</Card>
			</Col>
		</Row>
	</div>
</template>



<script>
	import { mapGetters } from "vuex";
	export default {
		name: "mainConton",
	
		data() {
			return {
				screenWidth: null,
				articleUrl: null,
				videoUrl: null
			};
		},
		computed: {
			...mapGetters(["screenSize"]),
			addPaddingCls() {
				return this.screenSize < 575 ? true : false;
			}
		},
		created() {
			this.getAricleUrl();
			this.getVideoUrl();
		},
		methods: {
			async getAricleUrl() {
				const {
					data: { articleUrlList }
				} = await this.$axios.get("api/articleUrl").catch(err => console.log(err));
				articleUrlList && ([this.articleUrl] = articleUrlList);
			},
			async getVideoUrl() {
				const {
					data: { videoUrlList }
				} = await this.$axios.get("api/videoUrl").catch(err => console.log(err));
				videoUrlList && ([this.videoUrl] = videoUrlList);
			}
		}
	};
</script>